<template>
  <div class="box" @click="choose(gallup.id)" :class="[gallup.selected ? 'selected': 'unselected']">
    <div class="desc">
        <p>{{ gallup.desc }}</p>
    </div>
  </div>
</template>
<script>
export default {
  data() {},
  props: {
    gallup: Object,
  },
  methods: {
    choose(id) {
      this.$emit("choose", id);
    },
  },
};
</script>

<style lang="css">
.selected {
    background-color: teal;
}
.unselected {
    background-color: antiquewhite;
}

.box:hover {
  cursor: pointer;
}
.box {
  display: inline-block;
  margin: 5px;
  width: 120px;
  height: 120px;
  padding: 15px;
  
}
.desc {
    display: inline-block;
    vertical-align: middle;
}

button {
  border: 1px;
  background-color: burlywood;
  width: 100px;
}
</style>


